import { useState, useMemo, useEffect, createContext } from 'react';
import { FiSearch, FiSun, FiMoon } from 'react-icons/fi';
import {
  TextField,
  Select as MUISelect,
  MenuItem,
  FormControl,
  InputLabel,
  IconButton,
} from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';
import { lightTheme, darkTheme } from './theme';
import diseaseData from './data/disease.json';
import regionData from './data/region.json';
import medicationData from './data/medication.json';
import vaccinationData from './data/vaccination.json';
import etcData from './data/etc.json';

export const ThemeContext = createContext();

const allData = [
  ...diseaseData,
  ...regionData,
  ...medicationData,
  ...vaccinationData,
  ...etcData,
];

function App() {
  const [query, setQuery] = useState('');
  const [filterType, setFilterType] = useState('');
  const [baseDate, setBaseDate] = useState(() =>
    new Date().toISOString().split('T')[0]
  );

  const formatDate = (date) => {
    const yyyy = date.getFullYear();
    const mm = String(date.getMonth() + 1).padStart(2, '0');
    const dd = String(date.getDate()).padStart(2, '0');
    return `${yyyy}${mm}${dd}`;
  };

  const getInitialTheme = () => {
    const stored = localStorage.getItem('theme');
    if (stored) return stored;
    return window.matchMedia('(prefers-color-scheme: dark)').matches
      ? 'dark'
      : 'light';
  };

  const [theme, setTheme] = useState(getInitialTheme);

  useEffect(() => {
    const media = window.matchMedia('(prefers-color-scheme: dark)');
    const systemChange = () => {
      const stored = localStorage.getItem('theme');
      if (!stored) {
        setTheme(media.matches ? 'dark' : 'light');
      }
    };
    media.addEventListener('change', systemChange);
    return () => media.removeEventListener('change', systemChange);
  }, []);

  useEffect(() => {
    document.documentElement.classList.remove('light', 'dark');
    document.documentElement.classList.add(theme);
    localStorage.setItem('theme', theme);
  }, [theme]);

  const toggleTheme = () => {
    setTheme((prev) => (prev === 'light' ? 'dark' : 'light'));
  };

  const results = useMemo(() => {
    const lower = query.toLowerCase();
    return allData.filter((item) => {
      if (!query) {
        if (filterType && item.type !== filterType) return false;
        return true;
      }

      const nameMatch = item.name.toLowerCase().includes(lower);
      const aliasMatch = (item.aliases || []).some((alias) =>
        alias.toLowerCase().includes(lower)
      );
      return nameMatch || aliasMatch;
    });
  }, [query, filterType]);

  return (
    <ThemeContext.Provider value={{ theme, toggleTheme }}>
      <ThemeProvider theme={theme === 'light' ? lightTheme : darkTheme}>
      <div className="App relative text-center p-8 space-y-6 max-w-3xl mx-auto">
        <h1 className="text-2xl font-bold">   </h1>

        <IconButton
          onClick={toggleTheme}
          aria-label=" "
          className="theme-toggle absolute top-4 right-4"
        >
          {theme === 'light' ? <FiMoon /> : <FiSun />}
        </IconButton>

        <div className="flex justify-center items-center gap-3">
          <FiSearch />
          <TextField
            variant="outlined"
            placeholder=" "
            value={query}
            onChange={(e) => setQuery(e.target.value)}
            size="small"
          />
        </div>

        <div className="flex justify-center items-center gap-3">
          <FormControl size="small" className={query ? 'invisible' : ''}>
            <InputLabel id="filter-label"></InputLabel>
            <MUISelect
              labelId="filter-label"
              label=""
              value={filterType}
              onChange={(e) => setFilterType(e.target.value)}
            >
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
              <MenuItem value=""></MenuItem>
            </MUISelect>
          </FormControl>
          <TextField
            type="date"
            value={baseDate}
            onChange={(e) => setBaseDate(e.target.value)}
            size="small"
          />
        </div>



        <ul className="result-list list-none mt-8 flex flex-col items-center space-y-4">
          {results.map((item) => {
            const period = item.restriction_period_days;
            const type = item.restriction_type;

            let periodText;
            if (type === 'permanent') {
              periodText = ' ';
            } else if (type === 'conditional') {
              periodText = ' ';
            } else if (period === 0) {
              periodText = '  ';
            } else if (period > 0) {
              periodText = ` : ${period}`;
            } else {
              periodText = ' ';
            }

            let message;
            if (type === 'permanent') {
              message = ' ';
            } else if (type === 'conditional') {
              message = '  ';
            } else if (period === 0) {
              message = ' ';
            } else if (period > 0) {
              const base = new Date(baseDate);
              base.setDate(base.getDate() + period);
              message = formatDate(base);
            } else {
              message = ' ';
            }

            const colorClass = message === ' '
              ? 'text-red-500 dark:text-red-400'
              : 'text-green-500 dark:text-green-400';

            return (
              <li key={item.id} className="result-item">
                <strong>{item.name}</strong> ({item.type}) - {item.restriction}
                <div className="period-text">{periodText}</div>
                <div className={`eligible-date ${colorClass}`}>{message}</div>
              </li>
            );
          })}

          {query && results.length === 0 && (
            <li className="no-result">  .</li>
          )}
        </ul>
      </div>
      </ThemeProvider>
    </ThemeContext.Provider>
  );
}

export default App;
